import React, { Suspense, lazy } from 'react';
import ReactDOM from 'react-dom';
import { ThemeProvider } from '@mui/material/styles';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import './index.css';
import Loading from 'components/Loading';
import PrelaunchTheme from './theme';
import { ScrollToTop } from './components/common';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import Authenticate from './hocs/require-authentication';

import './fonts/BadScript-Regular.ttf';

const bucket = process.env.REACT_APP_S3_BUCKET;
const authURL = process.env.REACT_APP_AUTH_URL;

const LazyApp = lazy(() => import('./App'));
const LazyCreateMemory = lazy(() =>
  import('./pages/memory/create/CreateMemory')
);
const LazyViewMemory = lazy(() => import('./pages/memory/view/ViewMemory'));
const LazyViewSharedMemory = lazy(() =>
  import('./pages/memory/view/ViewSharedMemory')
);
const LazyMemories = lazy(() => import('./pages/memories/Memories'));
const LazyCollageMaker = lazy(() => import('./pages/collage/Create'));
const LazyListCollages = lazy(() => import('./pages/collage/List'));
const LazyViewCollage = lazy(() => import('./pages/collage/View'));
const LazyBaseCollage = lazy(() => import('./pages/collage/Base'));
const LazyEntry = lazy(() => import('./pages/entry'));

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={PrelaunchTheme}>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <ScrollToTop />
        <Routes>
          <Route
            exact
            path="/"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate nextPath="/login">
                  <LazyEntry />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/login"
            element={
              <Suspense fallback={<Loading />}>
                <LazyApp authURL={authURL} />
              </Suspense>
            }
          />
          <Route
            path="/memories/:memoryMode?"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyMemories initMode="own" />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/collage/create"
            element={
              <Suspense fallback={<Loading />}>
                <LazyCollageMaker />
              </Suspense>
            }
          />

          <Route
            path="/collage/base"
            element={
              <Suspense fallback={<Loading />}>
                <LazyBaseCollage />
              </Suspense>
            }
          />
          <Route
            path="/collage/:collageID"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyViewCollage />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/collages"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyListCollages />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/memory/create"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyCreateMemory bucket={bucket} authURL={authURL} />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/memory/:memoryId"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyViewMemory bucket={bucket} authURL={authURL} />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="/memory/shared/:memoryId"
            element={
              <Suspense fallback={<Loading />}>
                <Authenticate>
                  <LazyViewSharedMemory />
                </Authenticate>
              </Suspense>
            }
          />
          <Route
            path="*"
            element={
              <Suspense fallback={<div>Loading...</div>}>
                <LazyApp authURL={authURL} />
              </Suspense>
            }
          />
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();
